body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

html, body {
    height: 100%;
    margin: 0;
    font-family: 'Quicksand', sans-serif;
}

.fullpage {
    height: 100vh;
    width: 100%;
    display: flex;
    flex: auto;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.icons-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.icon {
    margin: 10px;
}


h3 {
    font-size: 3rem;
}
.card {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
    height: 200px;
    width: 120px;
    background-color: white;
    margin: 10px;

    -webkit-transition: all 0.3s cubic-bezier(.25, .8, .25, 1);

    transition: all 0.3s cubic-bezier(.25, .8, .25, 1);
}

.card:hover {
    box-shadow: 0 5px 5px gray, 0 5px 5px gray;
}


.image-wrapper{
    width: 110px;
    height: 90px;
    text-align: center;
    padding: 5px;
}

img{
    max-width: 100%;
    max-height: 100%;
}

.skill-title-wrapper{
    padding: 5px;
    height: 100px;
    width: 110px;
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    justify-content: center;
}

.cards-wrapper {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;
}

h3 {
    font-size: 3rem;
}

#up {
    position: fixed;
    bottom: 0;
    margin: 20px;
    max-width: 30px;
    max-height: 30px;
}
.navbar {
    display: flex;
    flex-direction: row;
    height: 50px;
    position: fixed;
    width: 100%;
    background-color: rgba(255, 255, 255, 0.5);
    color: #000000;
    z-index: 10;
}

#theme {
    max-width: 30px;
    max-height: 30px;
}

.menuparts-left {
    position: fixed;
    right: 0;
    padding: 10px;
}
.menuparts {
    font-size: 20px;
    padding: 10px;
}

.menu {
     display: flex;
     flex-direction: row;
     height: 50px;
     width: 100%;
     background-color: rgba(255, 255, 255, 0.5);
     color: #000000;
 }
